import React, {useState, useEffect} from "react";
import '../styles/AdminFilter.css'

// 한글 초성을 추출하는 함수
const extractInitials = (text) => {
    const initialConsonants = [
        "ㄱ", "ㄲ", "ㄴ", "ㄷ", "ㄸ", "ㄹ", "ㅁ", "ㅂ", "ㅃ", "ㅅ", "ㅆ",
        "ㅇ", "ㅈ", "ㅉ", "ㅊ", "ㅋ", "ㅌ", "ㅍ", "ㅎ",
    ];
    return text
        .split("")
        .map((char) => {
            const code = char.charCodeAt(0) - 44032; // 유니코드 한글 범위 계산
            if (code < 0 || code > 11171) return char; // 한글이 아니면 그대로 반환
            const initialIndex = Math.floor(code / 588);
            return initialConsonants[initialIndex];
        })
        .join("");
};

const sequenceOptions = [
    {value: "", label: "전체"},
    {value: "1", label: "1회"},
    {value: "2", label: "2회"},
    {value: "3", label: "3회"},
    {value: "4", label: "4회"},
    {value: "5", label: "5회"},
    {value: "6", label: "6회"},
];

function checkKoreanInitialMatch(keyword) {
    return (name) => {
        const inviterInitials = extractInitials(name);
        const searchInitials = extractInitials(keyword);

        // 완전 문자열 매칭 또는 초성 매칭 확인
        return name.includes(keyword) || inviterInitials.startsWith(searchInitials);
    };
}

const AdminFilter = ({bookings, onFilterChange}) => {
    const [nameSearch, setNameSearch] = useState("");
    const [showSequence, setShowSequence] = useState("");
    const [inviteeSearch, setInviteeSearch] = useState("");
    const [filteredBookingCount, setFilteredBookingCount] = useState(0);

    useEffect(() => {
        const filtered = bookings.filter((booking) => {
            // 예약자 필터링
            const matchesBookName = !nameSearch
                || checkKoreanInitialMatch(nameSearch)(booking.bookName);

            // 회차 필터링
            const matchesSequence = !showSequence
                || booking.showSequence.toString() === showSequence;

            // 초대한 사람 필터링
            const matchesInvitee = !inviteeSearch
                || booking.inviters.some(checkKoreanInitialMatch(inviteeSearch));

            return matchesBookName && matchesSequence && matchesInvitee;
        });

        onFilterChange(filtered);
        setFilteredBookingCount(filtered.length);
    }, [bookings, nameSearch, showSequence, inviteeSearch, onFilterChange]);

    const resetFilters = () => {
        setNameSearch("")
        setShowSequence("");
        setInviteeSearch("");
    };

    return (
        <div className="filter-area">
            <div className="filter-bar">
                <div className="filter-section">
                    <div className="filter-input">
                        <label htmlFor="showSequence">회차</label>
                        <select
                            id="showSequence"
                            value={showSequence}
                            onChange={(e) => setShowSequence(e.target.value)}
                        >
                            {sequenceOptions.map((option) => (
                                <option key={option.value} value={option.value}>
                                    {option.label}
                                </option>
                            ))}
                        </select>
                    </div>
                    <div className="filter-input">
                        <label htmlFor="inviteeSearch">예매자</label>
                        <input
                            id="nameSearch"
                            type="text"
                            value={nameSearch}
                            onChange={(e) => setNameSearch(e.target.value)}
                            placeholder="예매자"
                        />
                    </div>
                    <div className="filter-input">
                        <label htmlFor="inviteeSearch">초대멤버</label>
                        <input
                            id="inviteeSearch"
                            type="text"
                            value={inviteeSearch}
                            onChange={(e) => setInviteeSearch(e.target.value)}
                            placeholder="초대멤버"
                        />
                    </div>
                </div>
                <div className="filter-stats">
                    <div>
                        <p>전체: {bookings.length}건</p>
                        <p>검색결과: {filteredBookingCount}건</p>
                    </div>
                </div>
            </div>
            <div>
                {nameSearch || showSequence || inviteeSearch ? (
                    <button className="reset-button" onClick={resetFilters}>
                        필터 초기화 ×
                    </button>
                ) : (<span/>)}
            </div>
        </div>
    );
};

export default AdminFilter;
