import React, {useCallback, useState} from 'react';
import Modal from "react-modal";
import AdminFilter from '../components/AdminFilter';
import AdminBookingTable from '../components/AdminBookingTable';
import AdminBookingDetails from '../components/AdminBookingDetails';
import '../styles/AdminBookingPage.css';
import {cancelBooking, updateBooking} from "../api/api";

const AdminBookingPage = ({bookings, addedBookingIds, onBookingRefresh}) => {
    const [updatedRowId, setUpdatedRowId] = useState(null);
    const [selectedBooking, setSelectedBooking] = useState(null);
    const [filteredBookings, setFilteredBookings] = useState(bookings);

    const handleRowClick = (booking) => {
        setSelectedBooking(booking);
    };

    const handleModalClose = () => {
        setSelectedBooking(null);
    };

    const handleDelete = (editedBooking) => {
        if (!selectedBooking) return;
        cancelBooking(editedBooking).then((value) => {
            setSelectedBooking(null);
            if (value?.status !== "SUCCESS") {
                return;
            }
            alert('예매가 취소되었습니다.');
        }).then(() => {
            onBookingRefresh();
        });
    };

    const handleSave = (editedBooking) => {
        if (!selectedBooking) return;
        updateBooking(editedBooking).then((value) => {
            setSelectedBooking(null);
            if (value?.status !== "SUCCESS") {
                return;
            }
            setUpdatedRowId(editedBooking.id);
            setTimeout(() => {
                setUpdatedRowId(null);
            }, 1000);
        }).then(() => {
            onBookingRefresh();
        });
    };

    const onFilterChange = useCallback((filtered) => {
        setFilteredBookings(filtered);
    }, []);

    return (
        <div className="admin-booking-page-container">
            <AdminFilter bookings={bookings} onFilterChange={onFilterChange}/>
            <AdminBookingTable
                bookings={filteredBookings}
                onRowClick={handleRowClick}
                updatedRowId={updatedRowId}
                addedBookingIds={addedBookingIds}
            />
            <Modal
                onRequestClose={handleModalClose}
                isOpen={!!selectedBooking}
                style={modalStyles}
            >
                {selectedBooking && (
                    <AdminBookingDetails
                        selectedBooking={selectedBooking}
                        onModalClose={handleModalClose}
                        onDelete={handleDelete}
                        onSave={handleSave}
                    />
                )}
            </Modal>
        </div>
    );
};

const modalStyles = {
    content: {
        maxWidth: "600px",
        width: "100%",
        top: "auto",
        bottom: "0",
        left: "50%",
        right: "auto",
        margin: "0",
        padding: "20px",
        borderRadius: "12px 12px 0 0",
        maxHeight: "50vh",
        overflowY: "auto",
        transform: "translate(-50%, 0)",
        boxSizing: "border-box",
    },
    overlay: {
        backgroundColor: "rgba(0, 0, 0, 0.5)",
    },
};

export default AdminBookingPage;
