import React from 'react';

const NotSupportedPage = () => {
    return (
            <div style={styles.container}>
                <h2>⚠️지원되지 않는 브라우저입니다 ⚠️</h2>
                <p>기본 브라우저로 열어주세요.</p>
            </div>
    );
};

const styles = {
    container: {
        textAlign: 'center',
        marginTop: '80px', // 헤더 아래로 이동
    },
};

export default NotSupportedPage;
