import React, {useEffect, useState} from 'react';
import AdminBookingPage from './AdminBookingPage';
import AdminLoginPage from "./AdminLoginPage";
import NoPermissionPage from "./NoPermissionPage";
import {fetchBookings} from "../api/api";
import {
    SUCCESS,
    LOADING,
    NO_LOGIN,
    NO_PERMISSION,
    UNKNOWN_STATUS,
    UNEXPECTED_ERROR,
} from '../constants/status'

const AdminIndexPage = ({status, setStatus}) => {
    const [bookings, setBookings] = useState([]);
    const [addedBookingIds, setAddedBookingIds] = useState(new Set());

    useEffect(() => {
        const fetchData = async () => {
            const {status, data} = await fetchBookings();
            const removeZeroBookingData = data.filter((booking) => booking.bookQuantity !== '0');
            const newRowIds = filterDiffRowIds(removeZeroBookingData, bookings);

            setStatus(status);
            setBookings(removeZeroBookingData);
            setAddedBookingIds(newRowIds)
        }
        if (status === LOADING) {
            fetchData().then(() => console.log('Data fetched successfully.'));
        }
    }, [status, setStatus]);

    const handleRefresh = () => {
        setStatus(LOADING);
    }

    const renderContent = () => {
        switch (status) {
            case NO_LOGIN:
                return <AdminLoginPage/>;
            case NO_PERMISSION:
            case UNKNOWN_STATUS:
            case UNEXPECTED_ERROR:
                return <NoPermissionPage/>;
            case LOADING:
            case SUCCESS:
                return <AdminBookingPage
                    bookings={bookings}
                    addedBookingIds={addedBookingIds}
                    onBookingRefresh={handleRefresh}
                />;
            default:
                return <div></div>; // 빈 영역 처리
        }
    };

    return <div>{renderContent()}</div>;
};

function filterDiffRowIds(newBookings, oldBookings) {
    if (oldBookings.length === 0) {
        return new Set();
    }
    const oldBookingIds = new Set(oldBookings.map((booking) => booking.id));
    return new Set(newBookings.map((booking) => booking.id).filter((id) => !oldBookingIds.has(id)));
}

export default AdminIndexPage;
