import React, {useState} from 'react';
import AdminHeader from './components/AdminHeader';
import AdminIndexPage from './pages/AdminIndexPage';
import {LOADING} from "./constants/status";
import NotSupportedPage from "./pages/NotSupportedPage";

const isKakaoInAppBrowser = (() => {
    const userAgent = navigator.userAgent || navigator.vendor || window.opera;
    return /KAKAOTALK/i.test(userAgent);
})();

function App() {
    const [status, setStatus] = useState(LOADING); // 초기 상태 설정
    return (
        <div>
            <AdminHeader status={status} setStatus={setStatus}/>
            {isKakaoInAppBrowser ?
                <NotSupportedPage/>
                : (
                    <div style={styles.container}>
                        <AdminIndexPage status={status} setStatus={setStatus}/>
                    </div>
                )
            }
        </div>
    );
}

const styles = {
    container: {
        paddingTop: '70px',
    },
};

export default App;
