import {getToken, isTokenExpired} from "../utils/auth";

export const fetchBookings = async () => {
    if (isTokenExpired()) {
        return {status: 'NO_LOGIN', data: []};
    }
    try {
        const token = getToken();
        const response = await fetch(`${process.env.REACT_APP_ENDPOINT_URL}/v1/tickets`, {
            method: 'GET',
            headers: {
                Authorization: `Bearer ${token}`,
                'X-Custom-Header': '7YK57Jik6529',
            },
        });

        if (response.status === 200) {
            return {status: "SUCCESS", data: await response.json()}
        } else if (response.status === 403) {
            return {status: 'NO_PERMISSION', data: []};
        } else {
            console.error('Unexpected response:', response);
            return {status: 'UNKNOWN_STATUS', data: []};
        }
    } catch (error) {
        console.error('API 호출 중 오류 발생:', error);
        return {status: 'UNEXPECTED_ERROR', data: []};
    }
};

export const updateBooking = async (editedBooking) => {
    const docId = editedBooking.id;
    if (isTokenExpired()) {
        return {status: 'NO_LOGIN'};
    }
    try {
        const token = getToken();
        const response = await fetch(`${process.env.REACT_APP_ENDPOINT_URL}/v1/tickets/${docId}`, {
            method: 'PATCH',
            headers: {
                Authorization: `Bearer ${token}`,
                "Content-Type": "application/json",
                'X-Custom-Header': '7YK57Jik6529',
            },
            body: JSON.stringify(editedBooking),
        });

        if (response.status === 200) {
            return {status: "SUCCESS"}
        } else if (response.status === 403) {
            return {status: 'NO_PERMISSION'};
        } else {
            console.error('Unexpected response:', response);
            return {status: 'UNKNOWN_STATUS'};
        }
    } catch (error) {
        console.error('API 호출 중 오류 발생:', error);
        return {status: 'UNEXPECTED_ERROR'};
    }
}

