import React from 'react';
import AdminBookingTableRow from "./AdminBookingTableRow";
import '../styles/AdminBookingTable.css';
import ExcelDownload from "./ExcelDownload";

function AdminBookingTable({bookings, onRowClick, updatedRowId, addedBookingIds}) {
    return (
        <div className="abp-table-responsive">
            <ExcelDownload ddata={bookings} />
            <table className="abp-table">
                <colgroup>
                    <col style={{width: '16%'}}/>
                    <col style={{width: '8%'}}/>
                    <col style={{width: '5%'}}/>
                    <col style={{width: '5%'}}/>
                    <col style={{width: '12%'}}/>
                    <col style={{width: '18%'}}/>
                    <col style={{width: '36%'}}/>
                </colgroup>
                <thead>
                <tr>
                    <th>예매시간</th>
                    <th>예매자명</th>
                    <th>수량</th>
                    <th>회차</th>
                    <th>휴대폰번호</th>
                    <th>초대멤버</th>
                    <th>응원메시지</th>
                </tr>
                </thead>
                <tbody>
                {bookings.map((bk) => (
                    <AdminBookingTableRow
                        key={bk.id}
                        bk={bk}
                        onRowClick={onRowClick}
                        isNew={addedBookingIds?.has(bk.id)}
                        isModified={updatedRowId === bk.id}
                    />
                ))}
                </tbody>
            </table>
        </div>
    );
}

export default AdminBookingTable;
