import React from "react";
import '../styles/HIghtlightedRow.css'

const AdminBookingTableRow = ({bk, onRowClick, isNew, isModified}) => {
    const invitersText = bk.inviters?.join(', ');
    const invitersCount = bk.inviters?.length;
    return (
        <tr id={bk.id}
            onClick={() => onRowClick(bk)}
            className={`${isModified ? "blink-blue" : ""} ${isNew && !isModified ? "highlight-yellow" : ""}`.trim()}
        >
            <td>{bk.bookAt}</td>
            <td>{bk.bookName}</td>
            <td>{bk.bookQuantity}</td>
            <td>{bk.showSequence}회</td>
            <td>{bk.phoneNumber}</td>
            <td style={{
                whiteSpace: 'nowrap',
                overflow: 'hidden',
                textOverflow: 'ellipsis'
            }}>
                {invitersCount <= 3 ? invitersText : `(${invitersCount})` + invitersText }
            </td>
            <td>{bk.cheeringMessage}</td>
        </tr>
    );
};

export default AdminBookingTableRow;
