import React from 'react';

const NoPermissionPage = () => {
    return (
            <div style={styles.container}>
                <h2>권한이 없습니다 ❌</h2>
                <p>관리자에게 문의하세요.</p>
            </div>
    );
};

const styles = {
    container: {
        textAlign: 'center',
        marginTop: '80px', // 헤더 아래로 이동
    },
};

export default NoPermissionPage;
