import React from 'react';

const AdminLoginPage = () => {
    return (
            <div style={styles.container}>
                <img
                        src={`${process.env.PUBLIC_URL}/KingOfMorac.jpg`}
                        alt="로그인 안내"
                        style={styles.image}
                />
                <h2>로그인을 해주세요 🔒</h2>
            </div>
    );
};

const styles = {
    container: {
        textAlign: 'center',
    },
    image: {
        width: '300px',
    },
};

export default AdminLoginPage;
